html {
    margin: 0;
    padding: 0;
}


body {
    font-size: 16px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}


.nav {
    display: flex;
    flex-direction: row;
    background-color: white;
}

 a {
    list-style: none;
    text-decoration: none;
    /* color: #6a6494; */
    font-size: 20px;
    line-height: 27px;
    margin-left: 1rem;
}

a:hover {
    color: #272343;
}


li {
    list-style: none;
}



.list {
    display: flex;
    flex-direction: row;
    margin-left: 1rem;
    background-color:#272343;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    padding-left:  4rem;
    padding-right: 4rem;
    /* width: 100%; */
    height: 100%;
    border-radius: 5px;
    margin-bottom: 2rem;
    margin-right: 2rem;
    margin-left: 2rem;
    color: white;
}


.firstList li:nth-of-type(1) {
    font-size: 18px;
}

.firstList li:nth-of-type(2) {
    font-size: 16px;
}

span {
    visibility: hidden;
}

.secondList{
    display: flex;
    flex-direction: row;
    margin-top: 1.5rem;
}

.secondList button {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    margin-left: 1rem;
    border: none;
    outline: none;
    background-color:#272343;
    cursor: pointer;
}



.secondList__one, .secondList__two {
    margin-left: 3rem;
    color: white;
}

.secondList__one:hover {
    color: white;
    background-color:  #FAAE2C;
}

.secondList__two:hover {
    color: white;
    background-color:  #FAAE2C;
}

.cart {
    margin-left: 3rem;
}

.cart:hover {
    color: white;
}

p {
    margin-top: 2rem;
}

p:nth-of-type(3) {
    margin-right: 1rem;
    margin-left: 1rem;
}


.styleList {
    display: flex;
    flex-direction: column;
}

.styleList a{
    color: black;
}